*{
    color: white;
}
.container{
    margin: 3em 0;
    line-height: 2em;
    background-color: rgb(0,0,0,0.4);
    width: 80%;
    margin-right: auto;
    margin-left: auto;
    padding: 0.3em 0;
}
.big{
    padding-top: 0.3em;
    font-size: 5em;
}
.minmax{
    padding: 0.5em 1em;   
    display:block;      
}
@media only screen and (min-width: 700px){
    .container{
        width: 60%;
    }
    .minmax{
        display:inline;
    }
}
@media only screen and (min-width:1024px){
    .container{
        margin-top: 7em;
        width: 50%;
    }
}